<template>
  <div style="display: flex" >
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/performance" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">签到情况</span>
        </el-menu-item>
        <el-menu-item index="/standard" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">操作标准</span>
        </el-menu-item>
        <el-menu-item index="/maintain" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">维护记录</span>
        </el-menu-item>
        <el-menu-item index="/checkRecord" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">点检记录</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 160px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <div style="padding: 20px 0 0 20px;width: 100%">
      <div>
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </div>
      <div style="margin: 10px 0">
        <el-button type="primary" plain @click="handleAdd" >新增</el-button>
        <el-button type="danger" plain @click="delBatch">批量删除</el-button>
        <el-button type="info" plain @click="exportData">批量导出</el-button>
      </div>
      <el-table :data="tableData" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="用户ID" prop="id" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="工段" prop="section" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="工号" prop="workNum"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="姓名" prop="name"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="所属部门" prop="department" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="签到时间" prop="signIn" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="到勤情况" prop="attendance" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="操作" align="center" width="180" fixed="right">
          <template v-slot="scope">
            <el-button size="mini" type="primary" plain @click="handleEdit(scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" plain  @click="del(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>

      <el-dialog title="新增签到信息" :visible.sync="fromVisible" width="30%">
        <el-form :model="form" label-width="80px" style="padding-right: 20px" :rules="rules" ref="formRef">
          <el-form-item label="工段" prop="section"><el-input v-model="form.section" placeholder="工段"></el-input></el-form-item>
          <el-form-item label="工号" prop="workNum"><el-input v-model="form.workNum" placeholder="工号"></el-input></el-form-item>
          <el-form-item label="姓名" prop="name"><el-input v-model="form.name" placeholder="姓名"></el-input></el-form-item>
          <el-form-item label="所属部门" prop="department"><el-input v-model="form.department" placeholder="所属部门"></el-input></el-form-item>
          <el-form-item label="到勤情况" prop="attendance"><el-input v-model="form.attendance" placeholder="到勤情况"></el-input></el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
  <!--  点检员工作情况-->

</template>

<script>
export default {
  name: 'Performance',
  data() {
    return {
      searchText: '',
      tableData: [],  // 所有的数据
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      name: '',
      total: 0,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      rules: {
        name: [
          { required: true, message: '请输入账号', trigger: 'blur' },
        ]
      },
      ids: []
    }
  },
  created() {
    this.load()
  },
  methods: {
    exportData(){   //批量导出数据
      if (!this.ids.length){  //没有选择行的时候，全部导出，或者根据我的搜索条件导出
        window.open(this.$baseUrl + '/performance/export?token=' + this.user.token + "&name=" + this.name)
      } else {   //[1,2,3] => '1,2,3'
        let idStr = this.ids.join(',')
        window.open(this.$baseUrl + '/performance/export?token=' + this.user.token + '&ids=' + idStr)
      }
    },
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/performance/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/performance/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleAdd() {   // 新增数据
      this.form = {}  // 新增数据的时候清空数据
      this.fromVisible = true   // 打开弹窗
    },
    handleEdit(row) {   // 编辑数据
      this.form = JSON.parse(JSON.stringify(row))  // 给form对象赋值  注意要深拷贝数据
      this.fromVisible = true   // 打开弹窗
    },
    save() {   // 保存按钮触发的逻辑  它会触发新增或者更新
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.$request({
            url: this.form.id ? '/performance/update': '/performance/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '200') {  // 表示成功保存
              this.$message.success('保存成功')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 弹出错误的信息
            }
          })
        }
      })
    },
    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(pageNum) {  // 分页查询
      if (pageNum)  this.pageNum = pageNum
      this.$request.get('/performance/selectByPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          name: this.name,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
  }
}
</script>

<style scoped>

</style>